import Login from './views/Login.vue'
import NotFound from './views/404.vue'
import Home from './views/Home.vue'
import Main from './views/Main.vue'
import Table from './views/nav1/Table.vue'
import Form from './views/nav1/Form.vue'
import user from './views/nav1/user.vue'
import systemUser from './views/system/systemUser.vue'
import systemAuthority from './views/system/systemAuthority.vue'
import systemMenuTypes from './views/system/systemMenuTypes.vue'
import systemMenuModule from './views/system/systemMenuModule.vue'
import userManage from './views/user/userManage.vue'
import echarts from './views/charts/echarts.vue'

let routes = [
    {
        path: '/',
        redirect: '/home', //重定向
        component: Home,
        hidden: true,
        iconCls:'fa fa-home',
        children: [
            { path: '/home', component: Main, name: 'home',hidden:true}
        ]
    },
    {
        path: '/login',
        component: Login,// 注册组件
        name: '',
        hidden: true
    },
    {
        path: '/404',
        component: NotFound,
        name: '',
        hidden: true
    },
    {
        path:'/user',
        name:'用户系统',
        component:Home,
        iconCls:'fa fa-user',
        children:[
            {path:'/user/userManage', name:'用户管理',component:userManage}
        ]
    },
    {
        path:'/operate',
        component: Home,
        name:'运营配置',
        iconCls:'fa fa-list-alt',
        children:[
            {path: '/operate/bootPage', name:'启动页', component:Table},
            {path: '/operate/bootPage', name:'礼物', component:Table},
            {path: '/operate/bootPage', name:'脸萌', component:Table},
            {path: '/operate/bootPage', name:'变声', component:Table},
            {path: '/operate/bootPage', name:'表情', component:Table},
            {path: '/operate/bootPage', name:'充值', component:Table},
            {path: '/operate/bootPage', name:'版本提醒', component:Table},
            {path: '/operate/bootPage', name:'系统小助手', component:Table},
            {path: '/operate/bootPage', name:'用户反馈', component:Table},
            {path: '/operate/bootPage', name:'帮助', component:Table},
            {path: '/operate/bootPage', name:'举报', component:Table}
        ]
    },
    {
      path:'/chat',
      component:Home,
      name:'陪聊',
      iconCls:'fa fa-smile-o ',
      children:[
          {path:'/chat/chatChat' , name:'陪聊1', component:Table }
      ]
    },
    {
        path:'/call',
        component:Home,
        name:'客服',
        iconCls:'fa fa-volume-control-phone',
        children:[
            {path:'/call/callCenter' , name:'客服', component:Table }
        ]
    },
    {
        path:'/',
        component:Home,
        name:'安全',
        iconCls:'fa fa-bell',
        children:[
            {path:'/secure/audit' , name:'审核', component:Table },
            {path:'/secure/monitor' , name:'监控', component:Table },
            {path:'/secure/sensitiveWords' , name:'敏感词', component:Table }
        ]
    },
    {
        path: '/system',
        component: Home,
        name:'系统配置',
        iconCls: 'fa fa-cog',
        children: [
            { path: '/system/systemUser', name:'系统用户管理', component: systemUser},
            // { path: '', name:'ttt', component: Table},
            { path: '/system/systemAuthority', name:'权限管理', component: systemAuthority},
            { path: '/system/systemMenuTypes', name:'菜单类别', component: systemMenuTypes},
            { path: '/system/systemMenuModule', name:'菜单模块', component: systemMenuModule},
        ]
    },
    {
        path: '*',
        hidden: true,
        redirect: { path: '/404' }
    }
];

export default routes;


