<template>
  <section>
    <div style="display:none;" class="landscape"></div>
    <div style="display:none;" class="filter"></div>
    <canvas style="display:none;" id="canvas" width="100%" height="100%"></canvas>
    <el-form :model="loginForm" :rules="rules2" ref="loginForm" label-position="left" label-width="0px" class="demo-ruleForm login-container">
      <h3 class="title">系统登录</h3>
      <el-form-item prop="account">
        <el-input type="text" v-model="loginForm.account" auto-complete="off" placeholder="账号"></el-input>
      </el-form-item>
      <el-form-item prop="checkPass">
        <el-input type="password" v-model="loginForm.checkPass" auto-complete="off" placeholder="密码"></el-input>
      </el-form-item>
      <el-checkbox v-model="checked" checked class="remember">记住密码</el-checkbox>
      <el-form-item style="width:100%;">
        <el-button type="primary" style="width:100%;" @click.native.prevent="handleSubmit2" :loading="logining">登录</el-button>
        <!--<el-button @click.native.prevent="handleReset2">重置</el-button>-->
      </el-form-item>
    </el-form>
  </section>

</template>

<script>
  import { requestLogin } from '../api/api';
  //import NProgress from 'nprogress'
  export default {
    data() {
      return {
        logining: false,
        loginForm: {
          account: '',
          checkPass: ''
        },
        rules2: {
          account: [
            { required: true, message: '请输入账号', trigger: 'blur' },
            //{ validator: validaePass }
          ],
          checkPass: [
            { required: true, message: '请输入密码', trigger: 'blur' },
            //{ validator: validaePass2 }
          ]
        },
        checked: true
      };
    },
    methods: {
      handleReset2() {
        this.$refs.loginForm.resetFields();
      },
      handleSubmit2(ev) {
        var _this = this;
        this.$refs.loginForm.validate((valid) => {
          if (valid) {
            //_this.$router.replace('/table');
            this.logining = true;
            //NProgress.start();
            var loginParams = { username: this.loginForm.account, password: this.loginForm.checkPass };
            requestLogin(loginParams).then(data => {
              this.logining = false;
              //NProgress.done();
              let { msg, code, user } = data;
              if (code !== 200) {
                this.$message({
                  message: msg,
                  type: 'error'
                });
              } else {
                sessionStorage.setItem('user', JSON.stringify(user));
                this.$router.push({ path: '/' });
              }
            });
          } else {
            console.log('error submit!!');
            return false;
          }
        });
      }
    },
      /**canvas 动画
      mounted:function(){
      function Star(id, x, y){
          this.id = id;
          this.x = x;
          this.y = y;
          this.r = Math.floor(Math.random()*2)+1;
          var alpha = (Math.floor(Math.random()*10)+1)/10/2;
          this.color = "rgba(255,255,255,"+alpha+")";
      }

          Star.prototype.draw = function() {
              ctx.fillStyle = this.color;
              ctx.shadowBlur = this.r * 2;
              ctx.beginPath();
              ctx.arc(this.x, this.y, this.r, 0, 2 * Math.PI, false);
              ctx.closePath();
              ctx.fill();
          }

          Star.prototype.move = function() {
              this.y -= .15;
              if (this.y <= -10) this.y = HEIGHT + 10;
              this.draw();
          }

          Star.prototype.die = function() {
              stars[this.id] = null;
              delete stars[this.id];
          }


          function Dot(id, x, y, r) {
              this.id = id;
              this.x = x;
              this.y = y;
              this.r = Math.floor(Math.random()*5)+1;
              this.maxLinks = 2;
              this.speed = .5;
              this.a = .5;
              this.aReduction = .005;
              this.color = "rgba(255,255,255,"+this.a+")";
              this.linkColor = "rgba(255,255,255,"+this.a/4+")";

              this.dir = Math.floor(Math.random()*140)+200;
          }

          Dot.prototype.draw = function() {
              ctx.fillStyle = this.color;
              ctx.shadowBlur = this.r * 2;
              ctx.beginPath();
              ctx.arc(this.x, this.y, this.r, 0, 2 * Math.PI, false);
              ctx.closePath();
              ctx.fill();
          }

          Dot.prototype.link = function() {
              if (this.id == 0) return;
              var previousDot1 = getPreviousDot(this.id, 1);
              var previousDot2 = getPreviousDot(this.id, 2);
              var previousDot3 = getPreviousDot(this.id, 3);
              if (!previousDot1) return;
              ctx.strokeStyle = this.linkColor;
              ctx.moveTo(previousDot1.x, previousDot1.y);
              ctx.beginPath();
              ctx.lineTo(this.x, this.y);
              if (previousDot2 != false) ctx.lineTo(previousDot2.x, previousDot2.y);
              if (previousDot3 != false) ctx.lineTo(previousDot3.x, previousDot3.y);
              ctx.stroke();
              ctx.closePath();
          }

          function getPreviousDot(id, stepback) {
              if (id == 0 || id - stepback < 0) return false;
              if (typeof dots[id - stepback] != "undefined") return dots[id - stepback];
              else return false;//getPreviousDot(id - stepback);
          }

          Dot.prototype.move = function() {
              this.a -= this.aReduction;
              if (this.a <= 0) {
                  this.die();
                  return
              }
              this.color = "rgba(255,255,255,"+this.a+")";
              this.linkColor = "rgba(255,255,255,"+this.a/4+")";
              this.x = this.x + Math.cos(degToRad(this.dir))*this.speed,
                  this.y = this.y + Math.sin(degToRad(this.dir))*this.speed;

              this.draw();
              this.link();
          }

          Dot.prototype.die = function() {
              dots[this.id] = null;
              delete dots[this.id];
          }


          let canvas  = document.getElementById('canvas'),
              ctx = canvas.getContext('2d'),
              WIDTH,
              HEIGHT,
              mouseMoving = false,
              mouseMoveChecker,
              mouseX,
              mouseY,
              stars = [],
              initStarsPopulation = 80,
              dots = [],
              dotsMinDist = 2,
              maxDistFromCursor = 50;

          setCanvasSize();
          init();

          function setCanvasSize() {
              WIDTH = document.documentElement.clientWidth,
                  HEIGHT = document.documentElement.clientHeight;

              canvas.setAttribute("width", WIDTH);
              canvas.setAttribute("height", HEIGHT);
          }

          function init() {
              ctx.strokeStyle = "white";
              ctx.shadowColor = "white";
              for (var i = 0; i < initStarsPopulation; i++) {
                  stars[i] = new Star(i, Math.floor(Math.random()*WIDTH), Math.floor(Math.random()*HEIGHT));
                  //stars[i].draw();
              }
              ctx.shadowBlur = 0;
              animate();
          }

          function animate() {
              ctx.clearRect(0, 0, WIDTH, HEIGHT);

              for (var i in stars) {
                  stars[i].move();
              }
              for (var i in dots) {
                  dots[i].move();
              }
              drawIfMouseMoving();
              requestAnimationFrame(animate);
          }

          window.onmousemove = function(e){
              mouseMoving = true;
              mouseX = e.clientX;
              mouseY = e.clientY;
              clearInterval(mouseMoveChecker);
              mouseMoveChecker = setTimeout(function() {
                  mouseMoving = false;
              }, 100);
          }


          function drawIfMouseMoving(){
              if (!mouseMoving) return;

              if (dots.length == 0) {
                  dots[0] = new Dot(0, mouseX, mouseY);
                  dots[0].draw();
                  return;
              }

              var previousDot = getPreviousDot(dots.length, 1);
              var prevX = previousDot.x;
              var prevY = previousDot.y;

              var diffX = Math.abs(prevX - mouseX);
              var diffY = Math.abs(prevY - mouseY);

              if (diffX < dotsMinDist || diffY < dotsMinDist) return;

              var xVariation = Math.random() > .5 ? -1 : 1;
              xVariation = xVariation*Math.floor(Math.random()*maxDistFromCursor)+1;
              var yVariation = Math.random() > .5 ? -1 : 1;
              yVariation = yVariation*Math.floor(Math.random()*maxDistFromCursor)+1;
              dots[dots.length] = new Dot(dots.length, mouseX+xVariation, mouseY+yVariation);
              dots[dots.length-1].draw();
              dots[dots.length-1].link();
          }
//setInterval(drawIfMouseMoving, 17);

          function degToRad(deg) {
              return deg * (Math.PI / 180);
          }
      }
      **/
  }

</script>

<style lang="scss" scoped>
  .login-container {
    position: fixed;
    top: 50%;
    left: 50%;
    /*box-shadow: 0 0px 8px 0 rgba(0, 0, 0, 0.06), 0 1px 0px 0 rgba(0, 0, 0, 0.02);*/
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: -155px auto auto -190px;
    width: 350px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;
    .title {
      margin: 0px auto 40px auto;
      text-align: center;
      color: #505458;
    }
    .remember {
      margin: 0px 0px 35px 0px;
    }
  }
  /* canvas 动画*/
 section {
    margin:0;
    overflow:hidden;
    width:100%;
    height:100%;
    /*cursor:none;*/
    background:black;
    background:linear-gradient(to bottom,#000 0%,#20a0ff 100%);
  }
  .filter {
    width:100%;
    height:100%;
    position:absolute;
    top:0;
    left:0;
    background:#ADD8E6;
    /*animation:colorChange 30s ease-in-out infinite;*/
    /*animation-fill-mode:both;*/
    mix-blend-mode:overlay;
  }
  @keyframes colorChange {
    0%,100% {
      opacity:1;
    }
    50% {
      opacity:.6;
    }
  }.landscape {
     position:absolute;
     bottom:0px;
     left:0;
     width:100%;
     height:100%;
     /*background-image:url('http://www.jq22.com/css/img/xkbg.png');*/
     background-size:1000px 250px;
     background-repeat:repeat-x;
     background-position:center bottom;
   }

</style>


